<template>
    <div class="home-nav">
        <van-nav-bar>
            <button class="home-logo" slot="left">品书</button>
            <div class="gender-button" slot="title">
                <button :class="{ active: maleActive }" @click="changeGenderBoy">男生</button>
                <button :class="{ active: femaleActive }" @click="changeGenderGirl">女生</button>
            </div>
            <van-icon name="column" slot="right" @click="$router.push({ path: '/bookshelf' })" />
            <van-icon name="user-circle-o" slot="right" @click="$router.push({ path: '/mycenter' })" />
        </van-nav-bar>
    </div>
</template>

<script>
//             <van-image
//                 width="90"
//                 height="26"
//                 src="data:image/png;base64,"
//                 slot="left"
//             />
export default {
    data() {
        return {
            maleActive: true,
            femaleActive: false
        };
    },
    components: {},
    methods: {
        changeGenderBoy() {
            if (this.maleActive == false && this.femaleActive == true) {
                this.maleActive = true;
                this.femaleActive = false;

                this.$store.commit("setChangeGender", {
                    male: this.maleActive,
                    female: this.femaleActive
                });
            } else {
                return 0;
            }
        },
        changeGenderGirl() {
            if (this.maleActive == true && this.femaleActive == false) {
                this.femaleActive = true;
                this.maleActive = false;

                this.$store.commit("setChangeGender", {
                    male: this.maleActive,
                    female: this.femaleActive
                });
            } else {
                return 0;
            }
        }
    }
};
</script>

<style lang="less">
.home-nav {
    .van-nav-bar {
        // box-sizing: border-box;
        background: rgb(184, 51, 32);
        .van-nav-bar__left {
            line-height:30px;
            left: 10px;
            button {
                border: none;
                outline: none;
                background: transparent;
                font-weight: 900;
                font-size: 40px;
                font-family: "隶书";
                color: #ffffff;
            }
        }
        .van-nav-bar__title {
            button {
                border: none;
                outline: none;
                line-height: 26px;
                border: 1px solid #ffffff;
                background: transparent;
                color: #ffffff;
                &:first-child {
                    border-radius: 26px 0 0 26px;
                }
                &:last-child {
                    border-radius: 0 26px 26px 0;
                }
                &.active {
                    background: #ffffff;
                    color: #999;
                }
            }
        }
        .van-icon {
            color: #ffffff;
            font-size: 1.5em;
            margin: 0 0 0 5px;
        }
    }
}
</style>
